import {createSelector, createSlice} from '@reduxjs/toolkit'

const initialState = {predefinedFilter: "all"}

const filterSlice = createSlice({
    name: 'filter',
    initialState,
    reducers: {
        updatePredefinedFilter: {
            reducer: (state, action) => {
                return {
                    ...state,
                    predefinedFilter: action.payload
                }
            },
            prepare: (filter) => {
                return {payload: filter}
            },
        }
    },
})

export const {updatePredefinedFilter} = filterSlice.actions
export default filterSlice.reducer

export const selectFilter = (state) => state.filter

export const selectPredefinedFilter = createSelector(
    selectFilter,
    (filter) => filter.predefinedFilter
)