import {WHATS_APP_CHAT_TEXT, WHATS_APP_NUMBER} from "../../../utils/ApplicationConstants";

function WhatsAppFloatingButton() {
    const href = `https://api.whatsapp.com/send?phone=${WHATS_APP_NUMBER}&text=${WHATS_APP_CHAT_TEXT}`
    return (
        <div>
            <a href={href} className="float" target="_blank">
                <i className="fa fa-whatsapp my-float"></i>
            </a>
        </div>
    );
}

export default WhatsAppFloatingButton;