import {createSelector, createSlice} from '@reduxjs/toolkit'
import admins from '../../resources/accounts/admins.json';

const initialState = {
    isAuthenticated: false,
    username: undefined,
    picture: undefined,
    email: undefined,
}

const userinfoSlice = createSlice({
    name: 'userinfo',
    initialState,
    reducers: {
        updateUserInfo: {
            reducer: (state, action) => {
                const {isAuthenticated, username, picture, email} = action.payload
                return {
                    ...state,
                    isAuthenticated: isAuthenticated,
                    username: username,
                    picture: picture,
                    email: email
                }
            },
            prepare: (isAuthenticated, username, picture, email) => {
                return {payload: {isAuthenticated, username, picture, email}}
            },
        }
    },
})

export const {updateUserInfo} = userinfoSlice.actions
export default userinfoSlice.reducer

export const selectUserinfo = (state) => state.userinfo

export const selectUserInfo = createSelector(
    selectUserinfo,
    (userinfo) => {
        return {
            isAuthenticated: userinfo.isAuthenticated,
            username: userinfo.username,
            picture: userinfo.picture,
            email: userinfo.email,
        }
    }
)

export const selectIsAdmin = createSelector(
    selectUserinfo,
    (userinfo) => userinfo.isAuthenticated && admins.includes(userinfo.email)
)
