import {selectProductById, selectProducts} from "../../redux/reducers/productSlice";
import {useDispatch, useSelector} from "react-redux";
import {resolve_image_path} from "../../utils/ApplicationConstants";
import {removeItemFromCart} from "../../redux/reducers/cartSlice";

function CartItem(props) {
    const [productId, quantity] = props.item;
    const product = selectProductById(
        useSelector(selectProducts), productId
    );
    const dispatch = useDispatch()
    const removeItem = () => dispatch(removeItemFromCart(productId))
    return (
        <>
            <div className="d-flex cartItem">
                <div className="flex-shrink-0">
                    <img src={resolve_image_path(product.image)} alt="" className="lazyload"/>
                </div>
                <div className="flex-grow-1 ms-3">
                    <div>{product.name}</div>
                    <div>{quantity} <i className="bi bi-x"></i> ${product.price}</div>
                </div>
                <div>
                    <button className="btn" onClick={removeItem}><i className="bi bi-trash"></i></button>
                </div>
            </div>
        </>
    );
}

export default CartItem;