import React, {useEffect, useState} from 'react';
import AddToCart from "../add-to-cart/AddToCart";
import Quantity from "../quantity/Quantity";
import {APPLICATION, resolve_image_path} from "../../utils/ApplicationConstants";
import ProductContext from "./ProductContext";
import {useDispatch, useSelector} from "react-redux";
import {fetchProducts, selectIsProductsLoading, selectProducts} from "../../redux/reducers/productSlice";
import {Link} from "react-router-dom";
import Filter from "../filter/Filter";
import {selectPredefinedFilter} from "../../redux/reducers/filterSlice";
import {predefinedFilters} from "../../utils/PredefinedFilters";

function Products() {
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(fetchProducts())
    }, [])
    const filterOptions = predefinedFilters[useSelector(selectPredefinedFilter)]

    const products = filteredProducts(useSelector(selectProducts), filterOptions)
    const isProductsLoading = useSelector(selectIsProductsLoading)
    const [quantity, setQuantity] = useState({})
    const defaultQuantity = 1

    function handleQuantityChange(pid, value) {
        const updatedQuantity = {...quantity, [pid]: value}
        setQuantity(updatedQuantity)
    }

    function filteredProducts(products, filterOptions) {
        const filteredProducts = {};
        Object.entries(products).forEach(([key, product]) => {
            let isValid = true;
            Object.entries(filterOptions).forEach(([option, value]) => {
                if (option === 'tags') {
                    isValid = isValid && (value ? value.some(tag => product.tags.includes(tag)) : true);
                } else if (option === 'minPrice') {
                    isValid = isValid && (!value || product.price >= value);
                } else if (option === 'maxPrice') {
                    isValid = isValid && (!value || product.price <= value);
                }
                // Handle future filter options here
            });
            if (isValid) {
                filteredProducts[key] = product;
            }
        });
        return filteredProducts;
    }

    function renderProducts() {
        return Object.entries(products).map((product, key) => {
            const [pid, productValues] = product
            const {name, price, image} = productValues
            return (
                <div className="product-card" key={key}>
                    <Link to={`/${APPLICATION}/products/${pid}`}>
                        <img
                            src={`https://raw.githubusercontent.com/mspetrochem/mspetrochem/main/images/200-ltr-drum-1920x1080.png`}
                            alt={name}
                            className="product-image lazyload"
                        />
                        <h5 className="product-name">{name}</h5>
                    </Link>
                    <p className="product-price">${price}</p>
                    <div className="product-qty-cart">
                        <ProductContext.Provider value={
                            {
                                productId: pid,
                                name: name,
                                price: price,
                                image: image,
                                quantity: quantity[pid] || defaultQuantity
                            }
                        }>
                            <Quantity quantityChange={handleQuantityChange}/>
                            <AddToCart/>
                        </ProductContext.Provider>
                    </div>
                </div>

            )
        });
    }

    return (
        <div className="products">
            <div className="container-fluid">
                <Filter/>
                <div className="product-list">
                    {isProductsLoading ? <h3>Loading...</h3>
                        : renderProducts()}
                </div>
            </div>
        </div>
    );
}

export default Products;
