import {useContext, useState} from "react";
import ProductContext from "../products/ProductContext";

function Quantity({quantityChange}) {

    const {productId} = useContext(ProductContext);
    const [quantity, setQuantity] = useState(1)

    function incrementQuantity() {
        Promise.resolve(
            setQuantity(quantity + 1)).then(() => {
                quantityChange(productId, quantity + 1)
            }
        )
    }

    function decreaseQuantity() {
        if (quantity > 1) {
            Promise.resolve(
                setQuantity(quantity - 1)).then(() => {
                quantityChange(productId, quantity - 1)
            })
        }
    }

    function updateQuantity(value) {
        if (value && /^\d*$/.test(value)) {
            Promise.resolve(setQuantity(Number(value))).then(() => {
                quantityChange(productId, Number(value))
            })
        }
    }

    return (
        <div className="input-group">
            <button className="btn btn-outline-secondary" type="button" onClick={decreaseQuantity}>
                <i className="bi bi-dash"></i>
            </button>
            <input id={`qty_${productId}`} type="text" className="form-control quantity-input" placeholder="Quantity"
                   aria-label="Product quantity" value={quantity} onChange={(e) => updateQuantity(e.target.value)}/>
            <button className="btn btn-outline-secondary" type="button" onClick={incrementQuantity}>
                <i className="bi bi-plus"></i>
            </button>
        </div>
    );
}

export default Quantity;