import {useDispatch, useSelector} from "react-redux";
import {selectUserInfo, updateUserInfo} from "../../redux/reducers/userinfoSlice";
import {Modal} from 'bootstrap'
import $ from 'jquery';
import UserAccount from "../user-account/UserAccount";
import GoogleLoginButton from "./GoogleLoginButton";
import {LOGO} from "../../utils/ApplicationConstants";

function Login() {

    const userInfo = useSelector(selectUserInfo)
    const dispatch = useDispatch()

    window.onSignIn = (googleUser) => {
        const profile = decodeJwtResponse(googleUser.credential)
        dispatch(updateUserInfo(
            true, profile.given_name, profile.picture, profile.email
        ))
        closeModal()
    }

    function decodeJwtResponse(credential) {
        return JSON.parse(atob(credential.split('.')[1]))
    }

    function closeModal() {
        try {
            Modal.getInstance(document.getElementById('signInModel')).hide()
            $('.modal-backdrop').remove()
        }
        catch(err) {}
    }

    return (
        <>
            {
                userInfo.isAuthenticated
                    ? <UserAccount/>
                    : <a href="/" data-bs-toggle="modal" data-bs-target="#signInModel"> Login </a>
            }

            <div className="modal fade" id="signInModel" tabIndex="-1" aria-labelledby="signInModelLabel"
                 aria-hidden="true">
                <div className="modal-dialog">
                    <div className="modal-content">
                        <div className="modal-header">
                            <img src={LOGO} alt="Logo" className="modal-header-logo lazyload"/>
                            <button type="button" className="btn-close" data-bs-dismiss="modal"
                                    aria-label="Close"></button>
                        </div>
                        <div className="modal-body">
                            <GoogleLoginButton/>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Login;