import {useParams} from "react-router-dom";
import {selectProductById, selectProducts} from "../../redux/reducers/productSlice";
import {useSelector} from "react-redux";
import {resolve_image_path} from "../../utils/ApplicationConstants";
import Quantity from "../quantity/Quantity";
import AddToCart from "../add-to-cart/AddToCart";
import ProductContext from "../products/ProductContext";
import React, {useState} from "react";

function ProductDetails() {
    const {productId} = useParams();
    const product = selectProductById(
        useSelector(selectProducts), productId
    );
    console.log(product);
    const {pid, name, image, secondaryImages, tags, price} = product;
    const [displayImage, setDisplayImage] = useState(resolve_image_path(image))
    return (
        <div className="product-details">
            <div className="container">
                <div className="product-section">
                    <div className="image-section">
                        <div className="primary-image">
                            <img src={`https://raw.githubusercontent.com/mspetrochem/mspetrochem/main/images/red-oxide-1-ltr-1200x750.webp`} alt={name}/>
                        </div>
                        <div className="secondary-images">
                            {
                                secondaryImages.map((secondaryImage) =>
                                    <div onClick={() => setDisplayImage(resolve_image_path(secondaryImage))}
                                         key={`key-${secondaryImage}`}>
                                        <img src={`https://raw.githubusercontent.com/mspetrochem/mspetrochem/main/images/red-oxide-1-ltr-1200x750.webp`} alt={name}/>
                                    </div>
                                )
                            }
                        </div>
                    </div>
                    <div className="options-section">
                        <h3 className="text-center">{name}</h3>
                        <ProductContext.Provider value={
                            {
                                productId: pid,
                                name: name,
                                price: price,
                                image: image,
                                quantity: 1
                            }
                        }>
                            <Quantity/>
                            <AddToCart/>
                        </ProductContext.Provider>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ProductDetails;