import {useDispatch, useSelector} from "react-redux";
import {selectPredefinedFilter, updatePredefinedFilter} from "../../redux/reducers/filterSlice";

function Filter() {
    const dispatch = useDispatch()
    const predefinedFilter = useSelector(selectPredefinedFilter)

    function setPredefinedFilter(type) {
        return () => dispatch(updatePredefinedFilter(type));
    }

    return (
        <>
            <div className="btn-group" role="group" aria-label="Basic radio toggle button group">
                <input type="radio" className="btn-check" name="btnradio" id="btnradio1" autoComplete="off"
                       onChange={setPredefinedFilter("all")} checked={predefinedFilter === "all"}/>
                <label className="btn btn-outline-primary" htmlFor="btnradio1">ALL</label>

                <input type="radio" className="btn-check" name="btnradio" id="btnradio2" autoComplete="off"
                       onChange={setPredefinedFilter("thinner")} checked={predefinedFilter === "thinner"}/>
                <label className="btn btn-outline-primary" htmlFor="btnradio2">Thinner</label>

                <input type="radio" className="btn-check" name="btnradio" id="btnradio3" autoComplete="off"
                       onChange={setPredefinedFilter("paints")} checked={predefinedFilter === "paints"}/>
                <label className="btn btn-outline-primary" htmlFor="btnradio3">Paint</label>
            </div>
        </>
    )
}

export default Filter