import {useContext} from "react";
import ProductContext from "../products/ProductContext";
import {useDispatch} from "react-redux";
import {addItemToCart} from "../../redux/reducers/cartSlice";

function AddToCart() {
    const product = useContext(ProductContext);
    const dispatch = useDispatch()

    function addOrUpdateCart() {
        const {productId, quantity} = product;
        dispatch(addItemToCart(productId, quantity))
    }

    return (
        <div className="input-group">
            <button className="btn btn-outline-secondary add-to-cart-btn" type="button" onClick={addOrUpdateCart}>
                <span className="form-label">Add to cart</span>
            </button>
        </div>
    );
}

export default AddToCart;