import {Link} from 'react-router-dom';
import Login from "../login/Login";
import Locale from "../locale/Locale";
import {useTranslation} from "react-i18next";
import {APPLICATION, LOGO} from "../../utils/ApplicationConstants";
import CartBadge from "../cart/CartBadge";
import {useSelector} from "react-redux";
import {selectIsAdmin} from "../../redux/reducers/userinfoSlice";

function Header() {
    const {t} = useTranslation();
    const isAdmin = useSelector(selectIsAdmin)

    return (
        <header>
            <div>
                <Link to={`/${APPLICATION}`}>
                    <img src={LOGO} alt="Logo" className="logo lazyload"/>
                </Link>
            </div>
            <nav>
                <ul>
                    <li>
                        <Locale/>
                    </li>
                    <li>
                        <Link to={`/${APPLICATION}/`}>{t('home')}</Link>
                    </li>
                    <li>
                        <Link to={`/${APPLICATION}/products`}>Products</Link>
                    </li>
                    {
                        isAdmin ?
                            <li>
                                <Link to={`/${APPLICATION}/stocks`}>Stocks</Link>
                            </li> : null
                    }
                    <li>
                        <Login/>
                    </li>
                    <li>
                        <CartBadge/>
                    </li>
                </ul>
            </nav>
        </header>
    );
}

export default Header;
