import {useDispatch} from "react-redux";
import {updateUserInfo} from "../../redux/reducers/userinfoSlice";
import {APPLICATION} from "../../utils/ApplicationConstants";

function Logout() {
    const dispatch = useDispatch()

    function handleLogout() {
        dispatch(updateUserInfo(false))
    }

    return (
        <>
            <a className="dropdown-item" href={`/${APPLICATION}`} onClick={handleLogout}>
                <i className="bi bi-box-arrow-right sign-out-icon"></i>
                <span>Sign out</span>
            </a>
        </>
    )
}

export default Logout;