import {useSelector} from "react-redux";
import {selectCartCount, selectCartItems} from "../../redux/reducers/cartSlice";
import {APPLICATION} from "../../utils/ApplicationConstants";
import {Link} from "react-router-dom";
import CartItem from "./CartItem";

function CartBadge() {
    const count = useSelector(selectCartCount);
    const cartItems = useSelector(selectCartItems);

    return (
        <>
            <button className="btn position-relative cart-button" type="button" data-bs-toggle="offcanvas"
                    data-bs-target="#cartCanvas" aria-controls="cartCanvas">
                <i className="bi bi-cart4 cart-icon"></i>
                <span className="position-absolute translate-middle badge rounded-pill bg-danger cart-count">
                    {count}
                    <span className="visually-hidden">unread messages</span>
                </span>
            </button>

            <div className="offcanvas offcanvas-end" tabIndex="-1" id="cartCanvas"
                 aria-labelledby="cartCanvas">
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title" id="cartCanvasHeader">Shopping Cart</h5>
                    <button type="button" className="btn-close btn-close-white" data-bs-dismiss="offcanvas"
                            aria-label="Close"></button>
                </div>
                <div className="offcanvas-body">
                    {
                        Object.entries(cartItems).map((item) =>
                            <CartItem item={item} key={`key-${item.productId}`}/>
                        )
                    }
                    <nav>
                        <ul>
                            <li>
                                <Link to={`/${APPLICATION}/cart`}>View cart</Link>
                            </li>
                            <li>
                                <Link to={`/${APPLICATION}/checkout`}>Checkout</Link>
                            </li>
                        </ul>
                    </nav>
                </div>
            </div>
        </>

    );
}

export default CartBadge;