import {createAsyncThunk, createEntityAdapter, createSelector, createSlice,} from '@reduxjs/toolkit'
import products from '../../resources/products/products.json';

const productsAdapter = createEntityAdapter()

const initialState = productsAdapter.getInitialState({
    loading: true,
    products: {}
})

// Thunk functions
export const fetchProducts = createAsyncThunk(
    'products/fetchProducts',
    async () => {
        return products;
    }
)

const productsSlice = createSlice({
    name: 'products',
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder
            .addCase(fetchProducts.pending, (state) => {
                state.loading = true
            })
            .addCase(fetchProducts.fulfilled, (state, action) => {
                state.loading = false
                state.products = action.payload
            })
    },
})

// Export actions creators
export const {} = productsSlice.actions

// Export reducers
export default productsSlice.reducer

// Export products selectors
const productsState = (state) => state.products

export const selectProducts = createSelector(
    productsState, (productsState) => {
        return productsState.products
    }
)
export const selectIsProductsLoading = createSelector(
    productsState, (productsState) => {
        return productsState.loading
    }
)
export const selectProductById = (products, productId) => {
    return products[productId]
}

