import {Navigate, Route, Routes} from 'react-router-dom';
import Header from '../header/Header';
import Home from '../home/Home';
import Products from "../products/Products";
import {APPLICATION} from "../../utils/ApplicationConstants";
import Cart from "../cart/Cart";
import Stocks from "../admin/stock/Stocks";
import {useSelector} from "react-redux";
import {selectIsAdmin} from "../../redux/reducers/userinfoSlice";
import ProductDetails from "../product-details/ProductDetails";
import Checkout from "../checkout/Checkout";
import Footer from "../footer/Footer";
import WhatsAppFloatingButton from "../buttons/whatsapp/WhatsAppFloatingButton";
import FAQ from "../faq/FAQ";
import PrivacyPolicy from "../policy/PrivacyPolicy";
import ReturnPolicy from "../policy/ReturnPolicy";
import ShippingPolicy from "../policy/ShippingPolicy";
import ContactUs from "../contact-us/ContactUs";

function App() {
    const isAdmin = useSelector(selectIsAdmin)
    return (
        <>
            <Header/>
            <Routes>
                <Route path={`/${APPLICATION}`}>
                    <Route index element={<Home/>}/>
                    {/*<Route path="products">*/}
                    {/*    <Route index element={<Products/>}/>*/}
                    {/*    <Route path=":productId" element={<ProductDetails/>}/>*/}
                    {/*</Route>*/}
                    <Route path="products" element={<Products/>}/>
                    <Route path="products/:productId" element={<ProductDetails/>}/>
                    <Route path="cart" element={<Cart/>}/>
                    <Route path="checkout" element={<Checkout/>}/>
                    <Route path="faq" element={<FAQ/>}/>
                    <Route path="privacy-policy" element={<PrivacyPolicy/>}/>
                    <Route path="return-policy" element={<ReturnPolicy/>}/>
                    <Route path="shipping-policy" element={<ShippingPolicy/>}/>
                    <Route path="contact-us" element={<ContactUs/>}/>
                    <Route path="stocks"
                           element={
                               isAdmin ? (
                                   <Stocks/>
                               ) : (
                                   <Navigate replace to={`/${APPLICATION}/`}/>
                               )
                           }
                    />
                </Route>
                <Route
                    path="*"
                    element={<Navigate to={`/${APPLICATION}/`} replace/>}
                />
            </Routes>
            <WhatsAppFloatingButton/>
            <Footer/>
        </>
    );
}

export default App;
